import React from 'react';

function MemeView(props){
	const styles = {picture:{position:'relative','width':'100%',height:"100%"},
					div:{'width':'550px',height:'500px','margin-left':'23px'}};
	return(
		<div style={styles.div}>
			<center>	
			<h1 id="title">MEME.NÈ</h1>
			<p id="slogan">Trình tạo meme tự động</p>
				<form>
				<div id="textHolder">
				 <div>		
					<p>Nhập từ hiện phía trên</p>
					<input placeholder="TỪ HIỆN BÊN TRÊN" id="textField" type="text" name="upperText" onChange={(event)=>{props.handleChange(event)}}/>
				</div>
				<div>	
					<p>Nhập từ hiện phía dưới</p>
					<input placeholder="TỪ HIỆN BÊN DƯỚI" id="textField" type="text" name="bottomText" onChange={(event)=>{props.handleChange(event)}}/>		
				</div>
				</div>
				</form>
				<br/>
			</center>
			<h1 id="upperText">{props.upperText}</h1>
			<h1 id="bottomText">{props.bottomText}</h1>
			<div style={{width:'100%',height:'100%'}}>
			<img style={styles.picture} src={props.img} alt="meme"/>
			</div>
			<br/>
			<button id="change-pic" onClick={function(){props.nextPicture()}}>ẢNH KHÁC NHỈ</button>
		</div>
		)
}
export default MemeView;