import React from 'react';
import memes from './data/memeData.js';
import MemeView from './components/memeView.js';
import './css/app.css';
class App extends React.Component{
	constructor(){
		super();
		this.state={
			imgID:0,
			img:"",
			upperText:"TỪ HIỆN BÊN TRÊN",
			bottomText:"TỪ HIỆN BÊN DƯỚI"
		}
		this.nextPicture = this.nextPicture.bind(this);
		this.handleChange = this.handleChange.bind(this);
	}
	
	componentDidMount(){
		for(let i = 0; i< memes.length;i++){
			if(this.state.imgID === memes[i].id){
				this.setState({img:memes[i].img});
			}
		}
	}

	handleChange(event){
		if(event.target.value.length<=35){
		this.setState({
			[event.target.name]:event.target.value.toUpperCase()
		})
		}else{
			alert("Vượt quá giới hạn từ cho phép (chỉ được 35 từ)");
		}
	}
	
	nextPicture(){
		this.setState(prevState=>{
			if(prevState.imgID<memes.length-2){
				return{
					imgID:prevState.imgID+1}
			}else{
				return{imgID:0}
				}
			});
		for(let i = 0; i< memes.length;i++){
			if(this.state.imgID === memes[i].id){
				this.setState({img:memes[i].img});
			}
		}
	}
	render(){
			const meme = <MemeView bottomText={this.state.bottomText} upperText={this.state.upperText} handleChange={this.handleChange} nextPicture={this.nextPicture} img={this.state.img}/>;
		return(
			<div>
				
				{meme}
			</div>
			);
	}
}
export default App;